@import 'Variable.scss';

.ForgotPassword{
   width: 100vw;
   width: 100%;
    height:100%;
    margin: 1rem auto;
}

.ForgotPasswordform{
    background-color:$light-blue;
    height: 90vh;
    box-shadow: 0px 0px 5px rgba($color: #000000, $alpha: 1.2);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0rem auto;
    margin-top: 2rem;
    padding: 1rem;


}
.ForgotPasswordContent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2{
        font-size: 2rem;
        font-weight: 400;
        font-family: $font2;
        padding: 0rem 1rem;
        color: rgba(0, 0, 0, 0.720);
        text-align: center;
        color: rgba(0, 0, 0, 0.720);
 
    }
    >div{
        svg{
            position: absolute;
             transform:translateY(2.3vmax);
             margin-left: 0.5rem;
             color: rgba(0, 0, 0, 0.720);
        
        }

        >input{
          width: 40vw;
          height: 8vh;
          margin-top: 1rem;
          padding-left: 2.5rem;
          border: none;
          outline: none;
          background-color: $white;
          border: 1px solid rgba(0, 0, 0, 0.720);
          border-radius: 10px;
          font-size: 1.3vmax;
          color: $black;
          &::placeholder{
            font-size: 1.30rem;
            font-family: $font2;
          }
        
        }
    }
}




@media screen and (max-width: 1200px){


    .ForgotPasswordContent{
        div{
            svg{
                transform:translateY(4vmax);
        }
        >input{
         
            width: 80vw;

            font-size: 2vmax;
          }
    }
}
}


@media screen and (max-width: 600px){
    .ForgotPasswordContent{
        div{
            svg{
                transform:translateY(4vmax);
   
            }
            >input{
         
              width: 80vw;

              font-size: 2vmax;
            }
        }
    }
  
}